@import "../../styles/globalstyles.css";
@import "../../styles/colors.css";

.profilePageWrap {
  width: 100vw;
  height: 100vh;
  background-color: var(--black1);
  display: flex;
  flex-direction: column;
}

.profileSectionPar {
  position: relative;
  top: 150px;
  overflow: auto;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 150px);
}

.profileSection {
  display: flex;
  width: 1024px;
  flex-wrap: wrap;
  height: 70%;
}
